:root {
  --background: #0e1015;
  --primary: #f11036;
  --secondary: #a0112d;
  --third: #181a20;
  --four: #f39200;
}

body {
  background-color: var(--background);
  color: white;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
.title {
  color: var(--primary);
  font-size: 7em;
}
.subTitle {
  color: white;
  font-size: 1.5em;
}
.pointer {
  cursor: pointer;
}
.margin-1-em {
  margin: 0 1em;
}
.margin-y {
  margin: 1em 0;
}
.margin-2-em {
  margin: 0 2em;
}

/* Heigths */
.titleHeight {
  height: 50vh;
}
.ulHeigth {
  height: 35vh;
}

/* form */
.search {
  grid-column: 4/7;
  outline: none;
  background-color: var(--third);
  height: 5vh;
  border: 1px solid var(--primary);
  color: var(--primary);
  text-align: center;
}
#las {
  grid-column: 4/5;
  justify-self: center;
}
#lan {
  grid-column: 5/6;
  justify-self: center;
}
/* Botones*/
.btn {
  display: flex;
  width: 8em;
  height: 2.3em;
  background-color: var(--primary);
  color: white;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
}

.btn:hover {
  background-color: var(--secondary);
}

.btnServer {
  cursor: pointer;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--primary);
  width: 3.5em;
  height: 1.8em;
}
.btnServer.activo {
  cursor: pointer;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary);
  width: 3.5em;
  height: 1.8em;
}

.btnSearch {
  grid-column: 3/4;
  border: none;
  background-color: var(--primary);
  width: 100%;
  height: 5vh;
  border-radius: 2px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  justify-self: flex-end;
}

.btnSearch:hover {
  background-color: var(--secondary);
}

.btnSesion {
  background-color: var(--primary);
  width: 80%;
  height: 4em;
  font-weight: bold;
  font-size: 1em;
  border-radius: 0.2em;
}
.btnSesion:hover {
  background-color: var(--secondary);
}

.btnDonate {
  margin: 1em 0;
  background-color: var(--primary);
  width: 30%;
  height: 2em;
  font-size: 2.5em;
}

/* Grid Properties*/
.grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}
.grid-center {
  justify-self: center;
}
.col-all {
  grid-column: 1/9;
}

/*Nav*/
#gg {
  grid-column: 1/2;
  color: var(--primary);
}
#info {
  grid-column: 6/7;
  text-decoration: underline;
  color: white;
}
#Donar {
  grid-column: 7/8;
  text-decoration: underline;
  color: white;
}
#Discord {
  grid-column: 8/9;
  color: white;
  text-decoration: underline;
}

/*Start botonesv*/

/* Loader */
.spinner {
  width: 100px;
  height: 100px;
  position: relative;
}
.spinner div {
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 100%;
  border: 8px solid transparent;
  border-top-color: var(--primary);
  border-radius: 50%;
  animation: spinnerOne 1.2s linear infinite;
}
.spinner div:nth-child(2) {
  border: 8px solid transparent;
  border-bottom-color: var(--primary);
  animation: spinnerTwo 1.2s linear infinite;
}

@keyframes spinnerOne {
  0% {
    transform: rotate(0deg);
    border-width: 10px;
  }
  50% {
    transform: rotate(180deg);
    border-width: 1px;
  }
  100% {
    transform: rotate(360deg);
    border-width: 10px;
  }
}
@keyframes spinnerTwo {
  0% {
    transform: rotate(0deg);
    border-width: 1px;
  }
  50% {
    transform: rotate(180deg);
    border-width: 10px;
  }
  100% {
    transform: rotate(360deg);
    border-width: 1px;
  }
}
/* Flex variantes*/
.flex-colum-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.flex-center-wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

/* Dodge Users Card*/
.usersDodge {
  padding: 1em 0;
  margin: 1em 1em;
  list-style-type: none;
  background-color: var(--third);
  width: 10em;
  border-radius: 5px;
  color: white;
  border: 2px solid white;
}
.usersd-name {
  text-decoration: underline;
  margin-bottom: 1em;
}
.dodge-advice {
  background-color: var(--primary);
  margin: 0 10%;
  font-size: 2em;
  height: 10vh;
  margin-top: 1em;
}
/* UserCard Analisis*/
.userCard {
  background-color: var(--primary);
  width: 40%;
  border-radius: 0.5em;
  padding: 2%;
  margin: 1em 0;
}
#leagueIMG {
  width: 15%;
}
.rcLinks {
  text-decoration: underline;
  color: var(--primary);
  margin-bottom: 2em;
}
/* Donar */
#donate {
  width: 19%;
  color: white;
}
#line {
  background-color: white;
  height: 10%;
  width: 35%;
  color: transparent;
}

/* champ card*/
.champCard {
  padding: 1em 1em;
  margin: 1em 1em;
  background-color: var(--third);
  width: 15em;
  height: 24em;
  border-radius: 5px;
  color: white;
  border: 2px solid var(--primary);
}

/* Rolcard */
.rolCard {
  padding: 1em 1em;
  margin: 1em 1em;
  background-color: var(--third);
  width: 14em;
  height: 20em;
  border-radius: 5px;
  color: white;
  border: 2px solid var(--primary);
}

/* Sesion */
#win {
  grid-column: 4/5;
}
#loss {
  grid-column: 5/6;
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 5em;
  }
  .subTitle {
    font-size: 1em;
  }
  .search {
    grid-column: 3/7;
  }
  .btn {
    width: 80%;
  }
  .btnDonate {
    width: 90%;
  }

  .dodge-advice {
    margin: 0 10%;
    font-size: 1.4em;
    height: 10vh;
  }
  .userCard {
    width: 90%;
    border-radius: 0.5em;
    margin: 1em 0;
  }
  #win {
    grid-column: 3/5;
  }
  #loss {
    grid-column: 5/7;
  }
  #line {
    width: 50%;
  }
}

@media screen and (max-width: 576px) {
  .title {
    font-size: 4em;
  }
  .subTitle {
    font-size: 1em;
  }
  .search {
    grid-column: 2/8;
  }
  .btnSearch {
    grid-column: 2/4;
  }

  #info {
    grid-column: 3/5;
    text-decoration: underline;
  }
  #Donar {
    grid-column: 5/7;
    text-decoration: underline;
  }
  #Discord {
    grid-column: 7/9;
    color: white;
  }
  .dodge-advice {
    margin: 0 2%;
    font-size: 1em;
    height: 10vh;
  }
  #win {
    grid-column: 2/5;
  }
  #loss {
    grid-column: 5/8;
  }
  #line {
    width: 80%;
  }
}
